import { DEFAULT_CURRENCY_ARR, } from "../../../../core/src/core/model/price/Currency";
export const CURRENCY_UPDATE = "CURRENCY_UPDATE";
export const currencyModule = {
    state: () => {
        return {
            active: DEFAULT_CURRENCY_ARR[0],
        };
    },
    mutations: {
        [CURRENCY_UPDATE](state, payload) {
            state.active = payload.currency;
        },
    },
};
