



















import Vue from "vue";
import Component from "vue-class-component";
import { DECK_SHUFFLE, DECK_SORT } from "@/store/modules/deck";

@Component({})
export default class YgoSorter extends Vue {
    sort() {
        this.$store.commit(DECK_SORT);
    }

    shuffle() {
        this.$store.commit(DECK_SHUFFLE);
    }
}
